 .Wrapper{
    position: relative;
    width: 420px;
    height: 450px;
    background: seagreen;
    border-radius: 10px;
    color: #fff;
    display: flex;
    align-items: center;
 }
 .Wrapper .form-box{
    width: 100%;
    padding: 40px;
 }
 
 form h1{
    font-size: 36px;
    text-align: center;
 }
 form.input-box{
position: relative;
width: 100%;
height: 50px;
margin: 30px 0;

 }
 .input-box input{
width: 100%;
height: 100%;
background: transparent;
border: none;
outline: none;
border: 2px solid rgba(255, 255, 255, .1);
border-radius: 40px;
font-size: 16px;
color: #fff;
padding: 20px 45px 20px 20px; 
 }
 .input-box input::placeholder{
    color: #fff;
 }
 

 form .remember-forgot{
    display: flex;
    justify-content: space-between;
    font-size: 14.5px;
    margin: -15px 0 15px;
 }
 .remember-forgot label input{
    accent-color: #fff;
    margin-right: 4px;
 }
 .remember-forgot a{
    color: #fff;
    text-decoration: none;
 }
 .remember-forgot a:hover{
    text-decoration: underline;
 }
 form button{
    width: 100%;
    height: 45px;
    background: #fff;
    border: none;
    outline: none;
    border-radius: 40px;
    box-shadow: 0 0 10px rgba(0, 0, 0, .1);
    cursor: pointer;
    font-size: 16px;
    color: #333;
    font-weight: 700;
 }
 